.contacts-modal__button-close {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 30px;
  height: 30px;
  background: $accent-secondary-color;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  padding: 0px;
  fill: $main-text-color;
  cursor: pointer;

  &:hover,
  &:focus {
    background: $accent-main-color;
    transition: background-color 250ms $cubic-timing;
  }
}

.contacts-modal__icon-close {
  position: absolute;
  top: 8px;
  right: 7px;
  transition: background-color 250ms $cubic-timing;
}

.contacts-modal__map {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

  &--tablet {
    @media screen and (max-width: 480px) {
      display: none;
    }
  }

  &--mobile {
    @media screen and (min-width: 480px) {
      display: none;
    }
  }
}

.modal__location-city {
  @include secondary-font(12px, 500, 22px);
  display: flex;
  justify-content: center;
  color: $accent-main-color;
  margin-top: 10px;
  margin-bottom: 10px;
  margin-left: auto;
  margin-right: auto;
}
